@import url("light.css");
html[class*=_light] .mode-dark,
html:not([class*=_dark]) .mode-dark {
  display: none !important;
}

html[class*=_dark] .mode-light {
  display: none !important;
}

* {
  transition: all 0.5s cubic-bezier(0.24, 0.1, 0.25, 0.92);
}

body {
  background-color: var(--md-sys-color-surface);
  font-family: "Archivo", serif !important;
  font-weight: 400;
  font-style: normal;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.top_bar {
  display: flex;
  height: 4rem;
  background-color: var(--md-sys-color-inverse-surface);
  align-items: center;
  font-size: 1.3rem;
}
.top_bar a {
  display: flex;
  align-items: center;
  color: var(--md-sys-color-inverse-on-surface);
  text-decoration: none;
  height: 100%;
  padding: 8px;
}
.top_bar a:hover {
  background-color: var(--md-sys-color-inverse-on-surface);
  color: var(--md-sys-color-inverse-surface);
}
.top_bar.expandable.expanded {
  height: 10rem;
}